.portfolio-page {
    padding-left: 250px;
    padding-right: 50px;
    width: calc(100% - 150px);
    position: initial;
    height: 100%;
    overflow: auto;

    h1.page-title {
        margin-left: 100px;
        margin-top: 100px;
    }

    .images-container {
        display: flex;
        gap: 15px;
        flex-wrap: wrap;
        padding-bottom: 100px;
        margin-right: 50%;
    }
    
    .image-box {
            position: relative;
            flex: 1 1 20%;
            height: 250px;
            overflow: hidden;
            border-radius: 15px;
            min-width: calc(25% - 5px);

            .portfolio-image{
                position: absolute;
                z-index: 2;
                width: 100%;
                height: 100%;
                object-fit: cover;
            }

            .content {
                position: absolute;
                width: 100%;
                z-index: 3;
                padding: 10px 20px;
                transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
                background: linear-gradient(
                    180deg,
                    rgba(212, 201, 95, 0.2) 0,
                    rgb(124, 119, 50)
                );
                bottom: -70px;
            }

            .title {
                margin-bottom: 0;
                margin-top: 0;
                color: black;
                font-size: 24px;
                font-weight: 500;
                line-height: 24px;
            }

            .description {
                font-size: 14px;
                margin-bottom: 5px;
                color: black;
                font-weight: 700;
            }

            .btn {
                margin-top: 30px;
                margin-bottom: 10px;
                padding: 0 23;
                height: 40px;
                line-height: 34px;
                border: 2px solid beige;
                border-radius: 4px;
                font-size: 14px;
                color: beige;
                background: transparent;
                text-transform: uppercase;
                font-weight: 700;
                transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
                cursor: pointer;
            }

            .btn:hover {
                transform: translateY(-3px);
                background: darkslategray;
            }

            &:after {
                content: "";
                background: linear-gradient(180deg, beige, black);
                position: absolute;
                left: 0;
                right: 0;
                bottom: 0;
                top: 0;
                width: 100%;
                height: 100%;
                z-index: 2;
                transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
                opacity: 0;
            }

            &:hover:after {
                opacity: 0.85;
            }

            &:hover .content {
                bottom: 0;
            }
        }
}